body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
    margin: 6px;
    height: 160px;
    width: 160px;
    border-radius: 10px;
    position: relative;
    padding: 10px;
    background: #e2bf0b;
    float: left;
}

.card:hover{
    background: #ff2400;
}

.img-size {
    height: 140px;
    width: 140px;
}

.content {
    margin-left: auto;
    margin-right: auto;
    width: 75%;
    display: block;
    background: #dbb676
}

